@tailwind base;
@tailwind components;
@tailwind utilities;

.drift-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 1.25rem;
}

.drift-card {
  border: 1.5px solid #e5e7eb;
  border-radius: 14px;
  padding: 1.25rem;
  background: #ffffff;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease, border-color 0.2s ease;
}

.drift-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.08);
  border-color: #cbd5e1;
}

.drift-card.selected {
  border-color: #111827;
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.12);
}

.pill {
  display: inline-flex;
  align-items: center;
  padding: 0.25rem 0.75rem;
  border-radius: 999px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.3px;
}

.pill-running {
  background: #dcfce7;
  color: #166534;
}

.pill-stopped {
  background: #fef9c3;
  color: #854d0e;
}

.pill-muted {
  background: #e5e7eb;
  color: #374151;
}

.stat-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5rem;
  font-family: "JetBrains Mono", ui-monospace, SFMono-Regular, Menlo, Monaco,
    Consolas, "Liberation Mono", "Courier New", monospace;
}

.stat-label {
  display: block;
  font-size: 0.75rem;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.stat-value {
  display: block;
  font-weight: 700;
  color: #111827;
}

.selected-panel {
  margin-top: 1.75rem;
  padding: 1.25rem 1.5rem;
  border: 1.5px dashed #cbd5e1;
  border-radius: 14px;
  background: #f8fafc;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

.snippet-tabs {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.snippet-tab {
  border: 1px solid #e5e7eb;
  background: #ffffff;
  padding: 0.5rem 0.9rem;
  border-radius: 10px;
  font-weight: 600;
  color: #374151;
  cursor: pointer;
  transition: all 0.2s ease;
}

.snippet-tab.active {
  background: #111827;
  color: #ffffff;
  border-color: #111827;
}

.code-snippet {
  background: #0f172a;
  color: #e2e8f0;
  border-radius: 16px;
  padding: 1.25rem;
  border: 1px solid #1e293b;
  box-shadow: 0 16px 40px rgba(15, 23, 42, 0.25);
}

.logs-scroll {
  max-height: 260px;
  overflow-y: auto;
}

.code-meta {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  margin-bottom: 0.75rem;
}

.code-snippet pre {
  margin: 0;
  white-space: pre-wrap;
  word-break: break-word;
  font-family: "JetBrains Mono", ui-monospace, SFMono-Regular, Menlo, Monaco,
    Consolas, "Liberation Mono", "Courier New", monospace;
}

.how-built-blurb {
  position: fixed;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  background: #ffffff;
  border: 3px solid #111;
  border-radius: 20px;
  padding: 1.75rem 2rem;
  max-width: 320px;
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.3);
  z-index: 100;
  cursor: pointer;
  transition: all 0.3s ease;
}

.how-built-blurb:hover {
  transform: translateY(-50%) scale(1.02);
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.35);
}

.blurb-story {
  margin-top: 0.75rem;
  color: #1f2937;
  line-height: 1.55;
  font-size: 0.98rem;
}

@media (max-width: 900px) {
  .stat-row {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  }
}

@media (max-width: 768px) {
  .how-built-blurb {
    display: none;
  }
}
